// components/Loading.js
'use client';

import { useState, useEffect } from 'react';

export default function Loading() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // เริ่มการโหลด
    const timer = setTimeout(() => {
      setLoading(false); // เปลี่ยนค่า loading หลังจาก 5 วินาที
    }, 5000);

    return () => clearTimeout(timer); // ทำความสะอาดเมื่อคอมโพเนนต์ถูกทำลาย
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <div className="flex items-center justify-center w-32 h-32 border-4 border-t-4 border-blue-500 border-solid rounded-full animate-spin"></div>
      </div>
    );
  }

  return null; // เมื่อโหลดเสร็จให้ไม่แสดงอะไร
}
